'use client';

import { useState, useEffect } from 'react';
import { useAccount, useReadContract, useWriteContract } from 'wagmi';
import { parseAbi, formatUnits } from 'viem';
import { UserIcon, ChatBubbleLeftRightIcon, CheckCircleIcon, XMarkIcon, BanknotesIcon, PaperAirplaneIcon } from '@heroicons/react/24/outline';
import { CONTRACT_ADDRESSES } from '@/config/contracts';
import { getUserDetailsFromContract } from '@/utils/contractReads';

const registryAbi = parseAbi([
  'function getAllUsers() external view returns (address[])',
  'function getUserDetails(address userAddress) external view returns (string memory ensName, string memory avatarHash, bool registered)',
])

const chatAbi = parseAbi([
  'function sendMessage(address to, string content) external',
  'function getConversation(address user1, address user2) external view returns ((address sender, address receiver, string content, uint256 timestamp)[])',
])

const GDOLLAR_TOKEN_ADDRESS = '0x62B8B11039FcfE5aB0C56E502b1C372A3d2a9c7A'; // Celo Mainnet

const ERC20_ABI = parseAbi([
  'function transfer(address to, uint256 amount) external returns (bool)',
  'function balanceOf(address account) external view returns (uint256)',
]);

interface Member {
  address: string
  ensName: string
}

interface MembersListProps {
  onSelectUser?: (user: Member) => void
  showChatButton?: boolean
}

interface Message {
  sender: string
  content: string
  timestamp: bigint
}

export default function MembersList({ onSelectUser, showChatButton = true }: MembersListProps) {
  const { address: currentUserAddress } = useAccount()
  const [members, setMembers] = useState<Member[]>([])
  const [selectedUser, setSelectedUser] = useState<Member | null>(null)
  const [showChatModal, setShowChatModal] = useState(false)
  const [showTipModal, setShowTipModal] = useState(false)
  const [newMessage, setNewMessage] = useState('')
  const [tipAmount, setTipAmount] = useState<string>('')
  const [messages, setMessages] = useState<Message[]>([])

  const { data: allUsers } = useReadContract({
    address: CONTRACT_ADDRESSES.registry,
    abi: registryAbi,
    functionName: 'getAllUsers',
  })

  const { data: conversation, refetch: refetchConversation } = useReadContract({
    address: CONTRACT_ADDRESSES.chat,
    abi: chatAbi,
    functionName: 'getConversation',
    args: currentUserAddress && selectedUser ? [currentUserAddress as `0x${string}`, selectedUser.address as `0x${string}`] : undefined,
  })

  const { writeContractAsync, isPending: isSending } = useWriteContract()

  useEffect(() => {
    if (allUsers && allUsers.length > 0) {
      const membersList: Member[] = []

      // Create members list
      for (const userAddress of allUsers) {
        if (userAddress.toLowerCase() !== currentUserAddress?.toLowerCase()) {
          membersList.push({
            address: userAddress,
            ensName: `${userAddress.slice(0, 6)}...${userAddress.slice(-4)}`
          })
        }
      }

      setMembers(membersList)

      // Fetch actual usernames from the contract
      allUsers.forEach((userAddress) => {
        if (userAddress.toLowerCase() !== currentUserAddress?.toLowerCase()) {
          // Use the contract reading utility to fetch actual username
          try {
            const fetchUsername = async () => {
              try {
                const userDetails = await getUserDetailsFromContract(userAddress)

                if (userDetails) {
                  const actualUsername = userDetails.registered && userDetails.ensName
                    ? userDetails.ensName
                    : `${userAddress.slice(0, 6)}...${userAddress.slice(-4)}`

                  // Update the member in the list
                  setMembers(prev => prev.map(member =>
                    member.address === userAddress
                      ? { ...member, ensName: actualUsername }
                      : member
                  ))
                }
              } catch (error) {
                console.error('Error fetching username from contract:', error)
              }
            }

            // Fetch with a small delay to avoid overwhelming the network
            setTimeout(fetchUsername, Math.random() * 1000)
          } catch (error) {
            console.error('Error setting up username fetch:', error)
          }
        }
      })
    }
  }, [allUsers, currentUserAddress])

  useEffect(() => {
    if (conversation) {
      const formattedMessages: Message[] = conversation.map((msg: any) => ({
        sender: msg.sender,
        content: msg.content,
        timestamp: BigInt(msg.timestamp)
      }))
      setMessages(formattedMessages)
    }
  }, [conversation])

  const isCurrentUser = (memberAddress: string) => {
    return currentUserAddress?.toLowerCase() === memberAddress.toLowerCase()
  }

  const handleUserClick = (member: Member) => {
    setSelectedUser(member)
    setShowChatModal(true)
    if (onSelectUser) {
      onSelectUser(member)
    }
  }

  const handleSendMessage = async () => {
    if (!selectedUser || !newMessage || !currentUserAddress) return

    try {
      await writeContractAsync({
        address: CONTRACT_ADDRESSES.chat,
        abi: chatAbi,
        functionName: 'sendMessage',
        args: [selectedUser.address as `0x${string}`, newMessage],
      })
      setNewMessage('')
      // Refetch conversation after sending
      setTimeout(() => refetchConversation(), 1000)
    } catch (error) {
      console.error('Failed to send message:', error)
    }
  }

  const handleTipClick = (member: Member) => {
    setSelectedUser(member)
    setShowTipModal(true)
  }

  const handleSendTip = async (amount: string) => {
    if (!selectedUser || !currentUserAddress || !amount) return

    try {
      const amountInWei = BigInt(amount) * BigInt(10 ** 18)
      await writeContractAsync({
        address: GDOLLAR_TOKEN_ADDRESS,
        abi: ERC20_ABI,
        functionName: 'transfer',
        args: [selectedUser.address as `0x${string}`, amountInWei],
      })
      setShowTipModal(false)
      setTipAmount('')
    } catch (error) {
      console.error('Failed to send tip:', error)
    }
  }

  if (members.length === 0) {
    return (
      <div className="bg-white rounded-lg shadow-md p-6">
        <div className="flex items-center space-x-2 mb-4">
          <UserIcon className="h-5 w-5 text-gray-600" />
          <h3 className="text-lg font-semibold text-gray-800">Community Members</h3>
        </div>
        <div className="text-center py-8">
          <UserIcon className="h-12 w-12 text-gray-300 mx-auto mb-3" />
          <p className="text-gray-500">No registered members yet</p>
          <p className="text-sm text-gray-400 mt-1">Be the first to join the community!</p>
          <div className="mt-4 p-3 bg-blue-50 rounded-lg">
            <p className="text-xs text-blue-700">
              <strong>How to get started:</strong>
            </p>
            <ol className="text-xs text-blue-600 mt-1 space-y-1">
              <li>1. Go to the Register tab</li>
              <li>2. Create your profile with any name</li>
              <li>3. Come back here to see all members</li>
              <li>4. Start chatting with other users!</li>
            </ol>
          </div>
        </div>
      </div>
    )
  }

  return (
    <>
      <div className="bg-white rounded-lg shadow-md p-6">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center space-x-2">
            <UserIcon className="h-5 w-5 text-gray-600" />
            <h3 className="text-lg font-semibold text-gray-800">Community Members</h3>
            <span className="bg-blue-100 text-blue-800 text-xs font-medium px-2 py-1 rounded-full">
              {members.length}
            </span>
          </div>
        </div>

        <div className="space-y-2 max-h-64 overflow-y-auto">
          {members.map((member, index) => {
            const isCurrentUserMember = isCurrentUser(member.address)
            return (
              <div
                key={member.address}
                className={`flex items-center justify-between p-3 rounded-lg transition-colors duration-200 ${isCurrentUserMember
                    ? 'bg-blue-50 border-2 border-blue-200'
                    : 'hover:bg-gray-50 cursor-pointer group'
                  }`}
              >
                <div className="flex items-center space-x-3 flex-1" onClick={() => !isCurrentUserMember && handleUserClick(member)}>
                  <div className="relative">
                    <div className={`w-8 h-8 rounded-full flex items-center justify-center ${isCurrentUserMember
                        ? 'bg-gradient-to-r from-blue-400 to-blue-600'
                        : 'bg-gradient-to-r from-pink-400 to-purple-500'
                      }`}>
                      <span className="text-white text-sm font-medium">
                        {member.ensName.charAt(0).toUpperCase()}
                      </span>
                    </div>
                    {isCurrentUserMember && (
                      <div className="absolute -top-1 -right-1 w-4 h-4 bg-blue-500 rounded-full flex items-center justify-center">
                        <CheckCircleIcon className="h-3 w-3 text-white" />
                      </div>
                    )}
                  </div>
                  <div>
                    <div className="flex items-center space-x-2">
                      <p className={`font-medium transition-colors ${isCurrentUserMember ? 'text-blue-900' : 'text-gray-900 group-hover:text-blue-600'
                        }`}>
                        {member.ensName}
                      </p>
                      {isCurrentUserMember && (
                        <span className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-full">
                          You
                        </span>
                      )}
                    </div>
                    <p className="text-xs text-gray-500 font-mono">
                      {member.address.slice(0, 6)}...{member.address.slice(-4)}
                    </p>
                  </div>
                </div>

                {showChatButton && !isCurrentUserMember && (
                  <div className="flex items-center space-x-2">
                    <button
                      onClick={(e) => {
                        e.stopPropagation()
                        handleTipClick(member)
                      }}
                      className="opacity-0 group-hover:opacity-100 bg-blockbelle-gold hover:bg-blockbelle-gold/80 text-white p-2 rounded-full transition-all duration-200 transform hover:scale-105"
                      title="Tip G$"
                    >
                      <BanknotesIcon className="h-4 w-4" />
                    </button>
                    <button
                      onClick={(e) => {
                        e.stopPropagation()
                        handleUserClick(member)
                      }}
                      className="opacity-0 group-hover:opacity-100 bg-blue-500 hover:bg-blue-600 text-white p-2 rounded-full transition-all duration-200 transform hover:scale-105"
                      title="Start chat"
                    >
                      <ChatBubbleLeftRightIcon className="h-4 w-4" />
                    </button>
                  </div>
                )}
              </div>
            )
          })}
        </div>

        <div className="mt-4 pt-4 border-t border-gray-100">
          <p className="text-xs text-gray-500 text-center">
            Click on any member to start a private conversation or send G$ tips
          </p>
        </div>
      </div>

      {/* Chat Modal */}
      {showChatModal && selectedUser && (
        <div className="fixed inset-0 bg-black/60 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl p-6 max-w-2xl w-full max-h-[80vh] flex flex-col shadow-2xl">
            <div className="flex items-center justify-between mb-4">
              <div>
                <h3 className="text-xl font-bold text-gray-900">Chat with {selectedUser.ensName}</h3>
                <p className="text-xs text-gray-500 font-mono">{selectedUser.address.slice(0, 10)}...{selectedUser.address.slice(-8)}</p>
              </div>
              <button
                onClick={() => setShowChatModal(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <XMarkIcon className="h-6 w-6" />
              </button>
            </div>

            <div className="flex-1 border rounded-lg p-4 mb-4 overflow-y-auto bg-gray-50 min-h-[300px] max-h-[400px]">
              {messages.length === 0 ? (
                <div className="text-center py-8">
                  <ChatBubbleLeftRightIcon className="h-12 w-12 text-gray-300 mx-auto mb-3" />
                  <p className="text-gray-500">No messages yet</p>
                  <p className="text-sm text-gray-400 mt-1">Start the conversation!</p>
                </div>
              ) : (
                <div className="space-y-3">
                  {messages.map((msg, index) => (
                    <div key={index} className={`flex ${msg.sender.toLowerCase() === currentUserAddress?.toLowerCase() ? 'justify-end' : 'justify-start'}`}>
                      <div className={`max-w-[70%] rounded-lg p-3 ${msg.sender.toLowerCase() === currentUserAddress?.toLowerCase()
                          ? 'bg-blockbelle-purple text-white'
                          : 'bg-white border border-gray-200'
                        }`}>
                        <p className="text-sm">{msg.content}</p>
                        <p className="text-xs opacity-70 mt-1">
                          {new Date(Number(msg.timestamp) * 1000).toLocaleTimeString()}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            <div className="flex space-x-2">
              <input
                type="text"
                value={newMessage}
                onChange={(e) => setNewMessage(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
                className="flex-1 border border-gray-300 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blockbelle-purple"
                placeholder="Type a message..."
              />
              <button
                onClick={handleSendMessage}
                disabled={isSending || !newMessage}
                className="gradient-blockbelle hover:opacity-90 text-white font-semibold px-6 py-2 rounded-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed flex items-center"
              >
                {isSending ? (
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                ) : (
                  <PaperAirplaneIcon className="h-5 w-5" />
                )}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Tip Modal */}
      {showTipModal && selectedUser && (
        <div className="fixed inset-0 bg-black/60 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl p-6 max-w-md w-full shadow-2xl">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-xl font-bold text-gray-900">Send G$ Tip</h3>
              <button
                onClick={() => setShowTipModal(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <XMarkIcon className="h-6 w-6" />
              </button>
            </div>

            <p className="text-sm text-gray-600 mb-4">
              Send G$ to <span className="font-semibold text-blockbelle-purple">{selectedUser.ensName}</span>
            </p>

            <div className="space-y-3 mb-4">
              <div className="grid grid-cols-3 gap-2">
                {['10', '50', '100'].map((amount) => (
                  <button
                    key={amount}
                    onClick={() => setTipAmount(amount)}
                    className={`py-2 px-4 rounded-lg font-semibold transition-all ${tipAmount === amount
                        ? 'gradient-blockbelle text-white'
                        : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      }`}
                  >
                    {amount} G$
                  </button>
                ))}
              </div>
              <input
                type="number"
                value={tipAmount}
                onChange={(e) => setTipAmount(e.target.value)}
                className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blockbelle-purple"
                placeholder="Or enter custom amount"
              />
            </div>

            <button
              onClick={() => handleSendTip(tipAmount)}
              disabled={!tipAmount || isSending}
              className="w-full gradient-blockbelle hover:opacity-90 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {isSending ? (
                <span className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                  Sending...
                </span>
              ) : (
                `Send ${tipAmount || '0'} G$`
              )}
            </button>
          </div>
        </div>
      )}
    </>
  )
}